<template lang="pug">
    div.fansList
        template(v-if="isLoaded")
            template(v-if="list && list.length")
                ul.fans-list-info
                    li.fans-item(v-for="(item,index) in list")
                        router-link.head-image(:to="{path : '/user/others', query:{ to_user_id : item.user_id}}")
                            img(v-lazy="item.user_image")
                        div.fans-information
                            div.fans-name 
                                span(v-text="item.user_name")
                                div.praise
                                    i.icon.iconfont.sex-man(v-if="item.user_sex == 1") &#xe661;
                                    i.icon.iconfont.sex-woman(v-if="item.user_sex == 2") &#xe662;
                                    img.icon-level(:src="iconLevel[index]")
                    <div v-if="allLoaded" class="no-more-data">无更多数据</div>
            template(v-else)
                null-data
</template>
<script>
import nullData from '@/components/nullData'
export default {
    props: ['list', 'isLoaded', 'allLoaded'],
    components: {
        nullData
    },
    computed: {
        iconLevel() {
            let arr_user_level = [];
            for (let val of this.list) {
                arr_user_level.push(require(`@/assets/img/rank/rank_${val.user_lv}.png`));
            }
            return arr_user_level;
        }
    }
}
</script>

<style lang="less" scoped>
@import '../../../assets/css/variable.less';
.fansList {
    .fans-list-info {
        padding-left: .5rem;
        background: #fff;
        .fans-item {
            height: 3.25rem;
            width: 100%;
            display: flex;
            align-items: center;
            .head-image {
                display: block;
                height: 2rem;
                width: 2rem;
                border-radius: 2.5rem;
                overflow: hidden;
                flex-shrink: 0;
                img {
                    width: 100%;
                    height: 100%;
                }
            }
            .fans-information {
                border-bottom: 1px solid #e6e8f1;
                width: 100%;
                height: 3.25rem;
                margin-left: .5rem;
                display: flex;
                align-items: center;
                .fans-name {
                    font-size: .65rem;
                    display: flex;
                    align-items: center;
                    width: 50%;
                    span {
                        color: @color-theme;
                        display: block;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                    }
                    .sex-woman {
                        color: @color-red;
                    }
                    .sex-man {
                        color: @color-blue;
                    }
                    .praise {
                        display: flex;
                        align-items: center;
                        .iconfont {
                            font-size: .75rem;
                            margin: 0 .25rem;
                        }
                        img {
                            width: 1.3rem;
                            height: .63rem;
                            vertical-align: middle;
                        }
                    }
                }
            }
        }
    }
}
</style>



