<template lang="pug">
    div.labourUnion-detail(v-if="isLoaded")
        div.labourUnion-detail-content 
            img.bgphoto(v-lazy="societyImage")
        div.user-content
            div.head-img
                img(v-lazy="societyImage")
            div.union-alias(v-text="societyName")
            div.union-name(v-text="`会长：${societyChairman}`")
            div.union-person(v-text="`公会人数：${userCount+fansCount}`")
        div.text-content
            p(v-text="societyExplain" :class="{'activeDown' : activeName == 'drop-down'}"   )
            div.shrink
               i.icon.iconfont.drop-down(v-if="! isShowFlexible"  @click="dropDown") &#xe60d;
               i.icon.iconfont.up(v-if="isShowFlexible" @click="up") &#xe60c;
        div.crossband
</template>
<script>
export default {
    props: ['societyImage', 'societyName', 'societyChairman', 'userCount', 'fansCount', 'societyExplain', 'isLoaded'],
    data() {
        return {
            isShowFlexible: false,
            activeName: 'drop-down'
        }
    },
    methods: {
        dropDown() {
            this.activeName = '';
            this.isShowFlexible = !this.isShowFlexible;
        },
        up() {
            this.activeName = 'drop-down';
            this.isShowFlexible = !this.isShowFlexible;
        }
    }
}
</script>
<style scoped lang="less">
@import '../../../assets/css/variable.less';
.labourUnion-detail {
    position: relative;
    .labourUnion-detail-content {
        width: 100%;
        height: 13.25rem;
        background-color: rgba(0, 0, 0, .85);
        .bgphoto {
            width: 100%;
            height: 100%;
            filter: blur(10px);
            opacity: .2;
        }
    }
    .user-content {
        width: 100%;
        position: absolute;
        top: 2rem;
        left: 0;
        z-index: 999;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        font-size: .7rem;
        color: #fff;
        .head-img {
            height: 4.75rem;
            width: 4.75rem;
            border-radius: 2.5rem;
            border: 2px solid #fff;
            img {
                height: 100%;
                width: 100%;
                border-radius: 2.5rem;
            }
        }
        .union-alias {
            padding: .5rem 0;
        }
        .union-person {
            padding: .5rem 0 0 0;
        }
    }
    .text-content {
        padding: .5rem .4rem 0 .4rem;
        background: #fff;
        p {
            color: #333;
            font-size: .6rem;
            text-overflow: ellipsis;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 2;
            overflow: hidden;
            word-wrap: break-word;
        }
        .activeDown {
            display: -webkit-box;
        }
        .shrink {
            text-align: center;
            .iconfont {
                font-size: .6rem;
                color: @fc-light;
            }
        }
    }
    .crossband {
        height: .5rem;
        width: 100%;
        background-color: #f8f8f8;
    }
}
</style>





