<template lang="pug">
    div.memberList
        template(v-if="isLoaded")
            template(v-if="list && list.length")
                ul.member-list-info
                    li.member-item(v-for="(item,index) in list")
                        router-link.head-image(:to="{path : '/user/others', query:{ to_user_id : item.user_id}}")
                            img(v-lazy="item.user_image")
                        div.member-information
                            div.member-name 
                                span(v-text="item.user_name") 
                                div.praise
                                    i.icon.iconfont.sex-man(v-if="item.user_sex == 1") &#xe661;
                                    i.icon.iconfont.sex-woman(v-if="item.user_sex == 2") &#xe662;
                                    img.icon-level(:src="iconLevel[index]")
                            div.member-agree-width
                                span.refuse(@click="judge(item.user_id,refuse)") 拒绝
                                span.agree(@click="judge(item.user_id,agree)") 同意
                    <div v-if="allLoaded" class="no-more-data">无更多数据</div>
            template(v-else)
                null-data
</template>
<script>

import nullData from '@/components/nullData'
import { Toast } from 'mint-ui'
export default {
    props: ['list', 'isLoaded', 'allLoaded'],
    data() {
        return {
            refuse: 0,
            agree: 1
        }
    },
    methods: {
        judge(user_id, status) {
            this.society_id = this.$route.query.society_id;
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/join_check' : this.api.join_check();
            this.axios.get(axiosUrl, {
                params: {
                    applyFor_id: user_id,
                    is_agree: status,
                    society_id: this.society_id
                }
            }).then(res => {
                if (res.data.status == 1) {
                    Toast('操作成功');
                    this.$emit('ToUpdate');
                }
                else {
                    Toast(res.data.error || '操作失败');
                }
            })
                .catch(err => {
                    Toast(err || '网络异常，请求失败');
                });

        },
        //- 同意
        // agree(user_id) {
        //     this.society_id = this.$route.query.society_id;
        //     let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/join_check' : this.api.join_check();
        //     this.axios.get(axiosUrl, {
        //         params: {
        //             applyFor_id: user_id,
        //             is_agree: this.is_agree,
        //             society_id: this.society_id
        //         }
        //     }).then(res => {
        //         if (res.data.status == 1) {
        //             if (res.data.applyFor_status == 1) {
        //                 Toast('申请加入，已提交');
        //                 this.$emit('ToUpdate');
        //             }
        //             else {
        //                 Toast(res.data.error || '操作失败');
        //             }
        //         }
        //         else {
        //             Toast(res.data.error || '操作失败');
        //         }
        //     })
        //         .catch(err => {
        //             Toast(err || '网络异常，操作失败');
        //         });
        // }

    },


    components: {
        nullData
    },
    computed: {
        iconLevel() {
            let arr_user_level = [];
            for (let val of this.list) {
                arr_user_level.push(require(`@/assets/img/rank/rank_${val.user_lv}.png`));
            }
            return arr_user_level;
        }
    }
}
</script>

<style lang="less" scoped>
@import '../../../assets/css/variable.less';
.memberList {
    .member-list-info {
        padding-left: .5rem;
        background: #fff;
        .member-item {
            height: 3.25rem;
            width: 100%;
            display: flex;
            align-items: center;
            position: relative;
            .head-image {
                display: block;
                height: 2rem;
                width: 2rem;
                border-radius: 2.5rem;
                overflow: hidden;
                flex-shrink: 0;
                img {
                    width: 100%;
                    height: 100%;
                }
            }
            .member-information {
                border-bottom: 1px solid #e6e8f1;
                width: 100%;
                height: 3.25rem;
                margin-left: .5rem;
                display: flex;
                align-items: center;
                .member-name {
                    width: 50%;
                    font-size: .65rem;
                    display: flex;
                    align-items: center;
                    span {
                        color: @color-theme;
                        display: block;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                    }
                    .sex-woman {
                        color: @color-red;
                    }
                    .sex-man {
                        color: @color-blue;
                    }
                    .praise {
                        display: flex;
                        align-items: center;
                        .iconfont {
                            font-size: .75rem;
                            margin: 0 .25rem;
                        }
                        img {
                            width: 1.3rem;
                            height: .64rem;
                            vertical-align: middle;
                        }
                    }
                }
                .member-agree-width {
                    position: absolute;
                    right: .5rem;
                    height: 1.1rem;
                    top: 50%;
                    margin-top: -.55rem;
                    span {
                        font-size: .5rem;
                        color: #fff;
                        padding: .2rem .4rem;
                        border-radius: .5rem;
                        text-align: center;
                        line-height: .2rem;
                        background-color: @color-theme;
                    }
                    .refuse {
                        margin-right: .25rem;
                        background-color: @color-yellow;
                        color: @fc-main;
                    }
                }
            }
        }
    }
}
</style>



